import Vue from 'vue'
import Router from 'vue-router'
// import loading from '@/components/loading'
import index from '@/components/index'
import sex from '@/components/sex'
import doing from '@/components/doing'
import sz from '@/components/sz'
import share from '@/components/share'
import end from '@/components/end'
import { endianness } from 'os';



Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/loading',
    //   name: 'loading',
    //   component: loading
    // },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/sex',
      name: 'sex',
      component: sex
    },
    {
      path: '/doing/:sex',
      name: 'doing',
      component: doing
    },
    {
      path: '/sz/:sex:choice',
      name: 'sz',
      component: sz,
      // children:[{
      //   path: '/share/:msg:num',
      //   name: 'share',
      //   component: share
      // }]
    },
    {
      path: '/share/:sex:num:msg',
      name: 'share',
      component: share
    },
    {
      path: '/end/:sex:num:msg',
      name: 'end',
      component: end
    },
    {
      path:'/*',
      redirect:'/index'
    }

  ]
})



